.modal-btn-div {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
  }
  
  .modal-btn-div-inner {
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
  }

  .modal-btn {
    font-size: 3vw;
    padding: 20px;
    background-color: #000;
    color: #fff;
    border: 3px solid black;
    border-radius: 5px;
    animation: inandout 2s infinite;
    -webkit-animation: inandout 2s infinite;
    -moz-animation: inandout 2s infinite;
    -ms-animation: inandout 2s infinite;
    box-shadow: 0 10px 16px 1px rgb(42, 42, 43);
  }

  @keyframes inandout {
    0% {
      transform: scale(1);
    }
    50% {
      transform: scale(1.1);
    }
    100% {
      transform: scale(1);
    }
  }

  button:focus {
    outline: none;
  }

  .modal-btn:hover {
    background-color: #fff;
    color: #000;
  }

  .modal-btn:active {
    background-color: #fff;
    color: #000;
  }

  .modal {
    position: fixed;
    z-index: 1;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    overflow: auto;
    background-color: rgba(255, 255, 255, 0.80);
  }
  
  .modal-section {
    position: relative;
    width: 60%;
    height: auto;
    margin: 5% auto;
    padding: 30px;
    border-style: solid;
    border-color: #787878;
    border-width: 5px 1px 1px 5px;
    background-color: white;
  }

  .clearfix {
    overflow: auto;
  }

  .clearfix::after {
    content: "";
    clear: both;
    display: table;
  }

  .cancel-btn {
    position: absolute;
    top: 1%;
    right: 1%;
    height: 26px;
    width: 26px;
    border-radius: 13px;
    font-size: 13px;
    color: #fff;
    background-color: #adadad;
    transition: all .2s ease-in-out;
  }

  .cancel-btn:hover {
    background-color: #000;
    transform: scale(1.2);
  }

  .modal-title-div {
    width: 100%;
    border-style: solid;
    border-color: #787878;
    border-width: 0 0 1px 0;
    margin: 5px 0;
  }

  .modal-title {
    display: inline;
    border-style: solid;
    border-color: #1c46b0;
    border-width: 0 0 3px 0;
    margin: 0;
    font-size: 2.8vw;
    font-weight: lighter;
  }

  .modal-desc {
    font-weight: lighter;
    font-size: 1vw;
  }

  .text-bolder {
    font-weight: 600;
  }

  .all-items-container {
    height: 100%;
    width: 100%;
    display: flex;
    justify-content: space-between;
    flex-direction: row;
    flex-wrap: wrap;
  }

  .item-container{
    width: 180px;
    margin: 20px 0;
    text-align: center;
  }

  .item-image {
    width: auto;
    height: 10vw;
  }

  .title-container {
    overflow: hidden;
    text-overflow: ellipsis;
    line-height: 2vw;
    max-height: 5vw;
    display: -webkit-box;
    -webkit-line-clamp: 2;
    -webkit-box-orient: vertical;
  }

  .item-title {
    font-size: 1vw;
    font-weight: 600;
    text-align: left;
    overflow: hidden;
    margin: 0;
  }

  .item-sku {
    font-size: .8vw;
    font-weight: lighter;
    text-align: left;
    margin: 0;
  }

  .item-price {
    font-size: 1vw;
    font-weight: 400;
    text-align: left;
    margin-top: 30px;
  }

  .addtocart-btn {
    text-transform: uppercase;
    width: 100%;
    padding: 10px;
    font-size: 1vw;
    background-color: #1c46b0;
    color: #fff;
    font-weight: 600;
    transition: all .2s ease-in-out;
  }

  .addtocart-btn:hover {
    transform: scale(1.1);
  }

  .addtocart-btn:active {
    background-color: #fff;
    color: #1c46b0;
  }

  .save-btn {
    width: 100%;
    padding: 10px;
    font-size: 1vw;
    background-color: #1c46b0;
    color: #fff;
    font-weight: 600;
    transition: all .2s ease-in-out;
  }

  .save-btn:hover {
    transform: scale(1.03);
  }

  .save-btn:active {
    background-color: #fff;
    color: #1c46b0;
  }

  .checkout-and-nothanks-btns {
    float: right;
  }

  .checkout-btn {
    width: auto;
    font-size: 1vw;
    padding: 10px 15px;
    margin: 20px 0;
    color: #1c46b0;
    float: right;
    border: 1px solid #1c46b0;
    background-color: #fff;
    font-weight: 600;
    transition: all .2s ease-in-out;
  }

  .checkout-btn:hover {
    transform: scale(1.1);
  }

  .checkout-btn:active {
    background-color: #1c46b0;
    color: #fff;
  }

  .nothanks-btn {
    width: auto;
    font-size: .8vw;
    padding: 10px 20px;
    margin: 20px 0;
    color: #787878;
    float: left;
    border: none;
    text-decoration: underline;
    background: #fff;

  }


  @media only screen and (max-width: 650px)  {

    .modal-desc {
      display: none;
    }

    .all-items-container {
      margin-bottom: 10px;
    }

    .item-container{
      width: 100%;
      margin: 0 auto;
      border-style: solid;
      border-color: #000;
      border-width: 0 0 1px 0;
      text-align: left;
      padding-bottom: 20px;
    }
  
    .item-image {
      width: 15vw;
      height: auto;
      float: left;
      margin-right: 5px;
      margin-top: 5px;
      margin-bottom: 5px;
    }
  
    .title-container {
      display: inline-block;
      width: 50%;
      overflow: hidden;
      text-overflow: ellipsis;
      white-space: nowrap;
      text-align: left;
    }
  
    .item-title {
      text-overflow: ellipsis; 
      overflow: hidden; 
      white-space: nowrap;
      margin: 0;
    }
  
    .item-sku {
      font-size: .8vw;
      font-weight: lighter;
      text-align: left;
      margin: 0;
    }
  
    .item-price {
      font-size: 3vw;
      font-weight: 400;
      text-align: left;
      margin-top: 10px;
      margin-bottom: 0;
    }
  
    .addtocart-btn {
      text-transform: capitalize;
      width: auto;
      height: auto;
      font-size: 1vw;
      background-color: #fff;
      color: #000;
      font-weight: 400;
      border: none;
      padding: 0;
    }
  }